<!-- 这个组件向后端写入.md文件，同时要传送和保留一些文章的基本信息</div> -->
<template>
  <div class="mdEditor">
    <!-- prevent组织页面自动刷新？并执行test回调 -->
    <form @submit.prevent="test">
      <div>
        文章标题：<input
          type="text"
          v-model="title"
          placeholder="小心前面别留空格"
          style="width: 430px"
        />
      </div>
      <br />
      <div>
        文章分类：<input
          type="text"
          v-model="category"
          placeholder="小心前面别留空格"
        />
      </div>
      <br />
      <div>文章封面：<input type="file" @change="onchangemd" /></div>
      <br />
      <div>
        文章简介：
        <textarea
          rows="3"
          cols="50"
          v-model.lazy="intro"
          style="vertical-align: top"
        >
          我是一个文本框。</textarea
        >
      </div>
      <br />
    </form>

    <v-md-editor
      v-model="editorValue"
      :disabled-menus="[]"
      @upload-image="handleUploadImage"
      @save="save"
      height="calc(100vh - 150px)"
    ></v-md-editor>
  </div>
</template>
  
<script>
import axios from "axios";
export default {
  name: "mdEditorComponent",
  data() {
    return {
      editorValue: this.content != null ? this.content : "",
      title: "",
      category: "",
      picture: " ",
      intro: " ",
    };
  },
  props: {
    // 接收值父组件传递值
    //   content: String
  },
  methods: {
    //上传图片
    onchangemd(e) {
      console.log(e.target.files); //这个就是选中文件信息 ：一个fileList对象
      let formdata = new FormData();
      Array.from(e.target.files).map((item) => {
        console.log(item); //file对象
        this.picture = item.name; //将图片名保存，发送给后端
        formdata.append("file1", item); //将每一个文件图片都加进formdata
      });
      axios
        .post("http://43.139.56.159:3001/upload-single-file", formdata)
        .then((res) => {
          console.log(res);
        }); //打印响应体
    },

    test() {
      //v-model绑定的表单，一失去焦点就会收集到数据。
      console.log(this.title);
    },

    // v-md-editor 文件上传
    // handleUploadImage(event, insertImage, files) {
    //   console.log(files);
    //   // 上传
    //   for (let i = 0; i < files.length; i++) {
    //     this.crud.upload(files[i], "image/vMdEditor/").then((res) => {
    //       // 获取返回数据
    //       let data = res.data.data;
    //       // 添加图片到内容
    //       insertImage({
    //         url: data.url,
    //         desc: data.name,
    //       });
    //     });
    //   }
    // },

    // str2ab(str){
    //     var buf = new ArrayBuffer(str.length * 2); // 每个字符占用2个字节
    //     var bufView = new Uint16Array(buf);
    //     for (var i = 0, strLen = str.length; i < strLen; i++) {
    //       bufView[i] = str.charCodeAt(i);
    //     }
    //     return buf;
    // },
    save(text, html) {
      console.log("text", typeof text);
      console.log(html);
      //如果文章名字不为空，才能上传。避免后端出现.md的空文件
      if (this.title != " ") {
        //文章 id 与名字
        let file = new File([html], this.title + ".md"); //根据收集到表单
        let formData = new FormData();
        formData.set("file1", file);

        //上传文章（文件）
        axios
          .post("http://localhost:3001/upload-single-file", formData, {
            onUploadProgress: (progressEvent) => {
              const percentCompleted = Math.round(
                (progressEvent.loaded * 100) / progressEvent.total
              );
              console.log(`upload process: ${percentCompleted}%`);
            },
          })
          .then((res) => {
            console.log(res.data);
            console.log(res.data.url);
          });

        // 同时上传文章相关信息
        axios({
          method: "post",
          url: "http://localhost:3001/article/add",
          data: {
            // id: "1",//文章id数据库自动生成
            name: this.title,
            picture: this.picture,
            intro: this.intro,
            category: this.category,
            publish_time: " ",
          },
        });
      }
    },
  },

  watch: {
    editorValue: function (newNum) {
      // 修改调用者传入的值
      this.$emit("update:content", newNum);
    },
  },
  // mounted(){
  //       console.log(this.$store.state.user.isLogin);
  //   }
};
</script>
  
<style>
.mdEditor {
  width: 80%;
  margin-left: 10%;
}
</style>
  